#background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.programs {
  position: relative;
}
#start-bar .programs > ul { 
  display: none;
  position: absolute;
  top: 4rem;
  left: calc(100% - 6px);
  background: silver;
  list-style: none;
  padding: none;
  margin: 0;
}
.programs > ul label:after,
.programs > ul label:before {
  display: none;
}

#start-menu .programs ul img {
  width: 1rem;
  margin: 0 0.5rem 0 0;
}
#start-menu .programs ul label {
  margin: 0;
  width: 100%;
  padding: 0;
  height: auto;
  padding: 0.5rem;
  width: 14rem;
}
#start-bar .programs:hover > ul { 
  display: block;
}
#start-menu .programs ul li label:hover {
  box-shadow: none;   
}

#shutdown-screen [for="shutdown-screen-input"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 50% auto;
  cursor: pointer;
  z-index: 99999999999;
  background-repeat: no-repeat;
  background-color: black;
  background-position: 50% 50%;
}
.tab-box {
  min-width: 30%;
}
.tab-header {
  box-shadow: -2px -2px #e0dede, -2px 0 #e0dede, 0 -2px #e0dede, -4px 0 white, 0 -4px white, 2px 0 #818181, 2px -2px #818181, 4px 0 black;
  float: left;
  padding: 0.25rem;
}
.tab-box img {
  width: 1rem;
  float: left;
  padding: 0 0.25rem 1rem 0;
}
.tab-box .box {
  width: 100%;
  background: white;
  float: left;
  border: 2px inset;
  padding: 0;
  position: relative;
  left: -4px;
  list-style: none;
  margin: 0;
}

.tab-box .box label {
  padding: 0.25rem;
  box-sizing: border-box;
  cursor: pointer;
  width: 100%;
  float: left;
}

.help .content {
  background: white;
  width: calc(100% - 2rem);
  border: 2px inset;
  margin: 0 0 0 1rem;    
  min-height: 16rem;
  overflow: scroll;
  max-height: 16rem;
  top: 1.6rem;
}
.help .tab-box-content {
  display: flex;
  margin: 1rem;
}
.tab-box-content .content > div {
  display: none;
}
